import { Component, OnInit } from '@angular/core';
import { EmployeeService } from '../services/employee.service';

@Component({
  selector: 'app-emp-reg',
  templateUrl: './emp-reg.component.html',
  styleUrls: ['./emp-reg.component.css']
})
export class EmpRegComponent implements OnInit {

  selectedDepartment:string = '';
  constructor(private _employeeService: EmployeeService) { }

  ngOnInit() {
  }

  registerEmployee(regEmployeeForm){
    var employee = regEmployeeForm.value;
    var birthdate = employee.birthdate;
    console.log(birthdate);
    employee=JSON.stringify(employee);
    this._employeeService.saveEmployee(employee);
  }
}



